var string = "<h3 class='form-title'>Edit Task</h3>\n  <table>\n    <tr class='form-group'>\n      <td><label for='title'>Title: </label></td>\n      <td><input class='form-control' type='text' name='title' title='Title' value='<%= model.get('title') %>' placeholder='Title (required)'></td>\n    </tr>\n    <tr class='form-group'>\n      <td><label for='deadline'>Deadline: </label></td>\n      <td><input class='form-control' type='date' name='deadline' title='Deadline' value='<%= model.get('deadline') %>' placeholder='Deadline'></td>\n    </tr>\n    <tr class='form-group'>\n      <td><label for='status'>Status: </label></td>\n      <td>\n        <select class='form-control half-width left' name='status' title='Status' value='<%= model.get('status') %>' placeholder='Status'>\n          <option>New</option>\n          <option>In Progress</option>\n          <option>Blocking</option>\n          <option>Complete</option>\n        </select>\n      </td>\n    </tr>\n    <tr class='form-group'>\n      <td><label for='priority'>Priority: </label></td>\n      <td>\n        <select class='form-control half-width right' name='priority' title='Priority' value='<%= model.get('priority') %>' placeholder='Priority'>\n          <option>Urgent</option>\n          <option>High</option>\n          <option>Normal</option>\n          <option>Low</option>\n          <option>Not Important</option>\n        </select>\n      </td>\n    </tr>\n    <tr class='form-group'>\n      <td><label for='description'>Notes: </label></td>\n      <td><textarea class='form-control' name='description' title='Notes' value='<%= model.get('description') %>' placeholder='Notes'></textarea></td>\n    </tr>\n  </table>\n  <button type='submit' title='Update Task' class='btn btn-dark btn-lg btn-block'>Update Task</button>\n</div>";

module.exports = string;
